@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #fff;
  color: #042b5f;
  /* text-align: center; */
}

.valContainer{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f9f9f5;
    height: 100vh;
}

.noButton{
    background-color: red;
    outline: none;
    border: none;
    color: #fff;
    cursor: pointer;
    margin-left: 1rem;
    padding: .5rem;
    border-radius: .5rem;

}

.yesButton{
    background-color: green;
    border: none;
    outline: none;
    color: #fff;
    padding: .5rem;
    cursor: pointer;
    border-radius: .5rem;
}

.buttonContainer{
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: auto;
    
}

h3{
    text-align: center;
}

.img{
    display: block;
    margin: auto;
}

@media screen and (max-width: 500px) {
    .buttonContainer{
        flex-direction: column;
    }
    .noButton{
        margin-left: 0;
        margin-top: 1rem;
    }
}
